import React, {useState} from 'react';
import {Form, Avatar, Button, Input, Row, Col, message, Upload} from 'antd';
import {UserOutlined} from '@ant-design/icons';
import {ROW_GUTTER} from 'constants/ThemeConstant';
import Flex from 'components/shared-components/Flex'
import Loading from "../../../../../components/shared-components/Loading";
import axios from "axios";


const EditProfile = ({data, img, close}) => {

    const avatarEndpoint = 'https://www.mocky.io/v2/5cc8019d300000980a055e76'
    const [state, setState] = useState({
            name: data.name,
            email: data.email,
            username: data.username,
            phoneNumber: data.phone,
            website: data.website,
            address: data.address.street,
            city: data.address.city,
            zipcode: data.address.zipcode
        }
    )
    const [avatarUrl, setAvatarUrl] = useState({
        img: process.env.PUBLIC_URL + img
    })
    const [showLoading, setShowLoading] = useState(false)

    const onFinishFailed = errorInfo => {
        console.log('Failed:', errorInfo);
    };

    function getBase64(img, callback) {
        const reader = new FileReader();
        reader.addEventListener('load', () => callback(reader.result));
        reader.readAsDataURL(img);
    }

    const onUploadAvatar = info => {
        const key = 'updatable';
        if (info.file.status === 'uploading') {
            message.loading({content: 'Uploading...', key, duration: 1000});
            return;
        }
        if (info.file.status === 'done') {
            getBase64(info.file.originFileObj, imageUrl =>
                setState({
                    avatarUrl: imageUrl,
                }),
            );
            message.success({content: 'Uploaded!', key, duration: 1.5});
        }
    };

    const onRemoveAvatar = () => {
        setAvatarUrl({
            img: ''
        })
    }

    //Выгрузка данных на сервер и закрытие страницы
    const handleSaveChange = () => {
        setShowLoading(true)
        axios.post("https://jsonplaceholder.typicode.com/users#", state).then(response => {
            console.log(response)
        })
            .catch(error => {
                console.log(error.response)
            })
        setTimeout(() => {
            close()
            setShowLoading(false)
        }, 4000)
        // setShowLoading(false)
    }

    return (
        <div style={{padding: "20px"}}>
            <div style={{paddingBottom: "30px"}}>EDIT PROFILE</div>

            {showLoading && <Loading/>}

            <Flex alignItems="center" mobileFlex={false} className="text-center text-md-left">
                <Avatar size={90} src={avatarUrl.img} icon={<UserOutlined/>}/>
                <div className="ml-md-3 mt-md-0 mt-3">
                    <Upload onChange={onUploadAvatar} showUploadList={false} action={avatarEndpoint}>
                        <Button type="primary">Change Avatar</Button>
                    </Upload>
                    <Button className="ml-2" onClick={onRemoveAvatar}>Remove</Button>
                </div>
            </Flex>
            <div className="mt-4">
                <Form
                    name="basicInformation"
                    layout="vertical"
                    initialValues={
                        {
                            'name': data.name,
                            'username': data.username,
                            'email': data.email,
                            'phoneNumber': data.phone,
                            'website': data.website,
                            'address': data.address.street,
                            'city': data.address.city,
                            'zipcode': data.address.zipcode
                        }
                    }
                    onFieldsChange={(_, arr) =>
                        setState({
                            name: arr[0].value,
                            email: arr[1].value,
                            username: arr[2].value,
                            phoneNumber: arr[3].value,
                            website: arr[4].value,
                            address: arr[5].value,
                            city: arr[6].value,
                            zipcode: arr[7].value
                        })
                    }
                    onFinishFailed={onFinishFailed}
                >
                    <Row>
                        <Col xs={24} sm={24} md={24} lg={16}>
                            <Row gutter={ROW_GUTTER}>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Name"
                                        name="name"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Please input your name!',
                                            },
                                        ]}
                                    >
                                        <Input value={data.name}/>
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Username"
                                        name="username"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Please input your username!'
                                            },
                                        ]}
                                    >
                                        <Input/>
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Email"
                                        name="email"
                                        rules={[{
                                            required: true,
                                            type: 'email',
                                            message: 'Please enter a valid email!'
                                        }]}
                                    >
                                        <Input/>
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Phone Number"
                                        name="phoneNumber"
                                    >
                                        <Input/>
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Website"
                                        name="website"
                                    >
                                        <Input/>
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Address"
                                        name="address"
                                    >
                                        <Input/>
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="City"
                                        name="city"
                                    >
                                        <Input/>
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Zip code"
                                        name="zipcode"
                                    >
                                        <Input/>
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Button
                                type="primary"
                                htmlType="type"
                                onClick={handleSaveChange}>
                                Save Change
                            </Button>
                        </Col>
                    </Row>
                </Form>
            </div>
        </div>
    )
}

export default EditProfile
